"use client"

import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel"

export default function HomeCarousel() {
  const carouselImages = [
    {
      src: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/50af7fed-7231-40c8-a385-b530688a6271.png",
      alt: "Imagen de la fachada moderna de la empresa mostrando grandes ventanales y diseño contemporáneo",
      fallback: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/f717e8a6-cc36-43d8-9208-2f8b77da560a.png"
    },
    {
      src: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/f14b359c-f7c1-4cda-a496-345bdebfd1dc.png",
      alt: "Vista interior de las oficinas con espacios abiertos, escritorios modernos y ambiente profesional",
      fallback: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/eccb859f-2398-4ff6-bdbb-35a3d472c994.png"
    },
    {
      src: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/fb3f9237-3af6-49eb-b499-71ea9ed87cd9.png",
      alt: "Equipo de profesionales trabajando en colaboración en proyectos innovadores y tecnológicos",
      fallback: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/dad010d9-8f44-4b10-9b5b-536ab4870c31.png"
    }
  ]

  const handleImageError = (event: React.SyntheticEvent<HTMLImageElement, Event>, fallbackSrc: string) => {
    const target = event.target as HTMLImageElement
    target.src = fallbackSrc
  }

  return (
    <div className="w-full bg-gradient-to-b from-gray-50 to-white">
      <div className="container mx-auto px-4 py-8">
        <Carousel className="w-full max-w-5xl mx-auto">
          <CarouselContent>
            {carouselImages.map((image, index) => (
              <CarouselItem key={index}>
                <div className="relative aspect-[2/1] w-full overflow-hidden rounded-xl shadow-lg">
                  <img
                    src={image.src}
                    alt={image.alt}
                    className="w-full h-full object-cover transition-transform duration-300 hover:scale-105"
                    onError={(e) => handleImageError(e, image.fallback)}
                  />
                  <div className="absolute inset-0 bg-black/20 flex items-end">
                    <div className="p-6 text-white">
                      <h2 className="text-2xl font-bold mb-2">
                        {index === 0 && "Nuestra Empresa"}
                        {index === 1 && "Nuestras Instalaciones"}
                        {index === 2 && "Nuestro Equipo"}
                      </h2>
                      <p className="text-lg opacity-90">
                        {index === 0 && "Comprometidos con la excelencia y la innovación"}
                        {index === 1 && "Espacios diseñados para la productividad y colaboración"}
                        {index === 2 && "Profesionales dedicados a superar expectativas"}
                      </p>
                    </div>
                  </div>
                </div>
              </CarouselItem>
            ))}
          </CarouselContent>
          <CarouselPrevious className="left-4" />
          <CarouselNext className="right-4" />
        </Carousel>
      </div>
    </div>
  )
}
